.profile {
    display: grid;
    grid-template-columns: 1fr 3fr;
    /*margin-top: 120px;*/
}

.profile__list {
    list-style-type: none;
    max-width: 320px;
    margin-top: 120px;
}

.profile__item {
    width: 320px;
    height: 64px;
}

.profile__link {
    color: var(--text-dark);
    text-decoration: none;
}

.profile__link:hover {
    color: var(--text);
}

.profile__link_isActive {
    color: var(--text);
    text-decoration: none;
}
